"use client";

import { useEffect, useState, useRef } from "react";
import { User, AtSign, Calendar, Phone, Info, Bell, Languages, Ban, LogOut, Camera, X, Check, Loader2, ChevronRight } from "lucide-react";
import { Avatar } from "@/components/ui/avatar";
import { useAuth } from "@/contexts/auth-context";
import { ProfileService, Profile, UpdateProfileData } from "@/services/profile-service";
import { cn } from "@/lib/utils";
import { useToast } from "@/contexts/toast-context";

export default function ProfilePage() {
    const { user, logout } = useAuth();
    const { addToast } = useToast();
    const [profile, setProfile] = useState<Profile | null>(null);
    const [isEditing, setIsEditing] = useState(false);
    const [loading, setLoading] = useState(true);
    const [updating, setUpdating] = useState(false);
    const [showBlocked, setShowBlocked] = useState(false);
    const [formData, setFormData] = useState<UpdateProfileData>({});
    const fileInputRef = useRef<HTMLInputElement>(null);

    useEffect(() => {
        if (user) {
            fetchProfile();
        }
    }, [user]);

    const fetchProfile = async () => {
        if (!user) return;
        setLoading(true);
        const { data, error } = await ProfileService.getProfile(user.id);
        if (data) {
            setProfile(data);
            setFormData({
                full_name: data.full_name,
                username: data.username,
                age: data.age,
                phone: data.phone,
                bio: data.bio,
                gender: data.gender,
            });
        }
        setLoading(false);
    };

    const handleUpdate = async () => {
        if (!user) return;
        setUpdating(true);
        const { error } = await ProfileService.updateProfile(user.id, formData);
        if (!error) {
            await fetchProfile();
            setIsEditing(false);
            addToast("Profile updated successfully", "success");
        } else {
            addToast(error, "error");
        }
        setUpdating(false);
    };

    const handleAvatarUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file || !user) return;

        setUpdating(true);
        const { data: url, error } = await ProfileService.uploadAvatar(user.id, file);
        if (url) {
            const { error: updateError } = await ProfileService.updateProfile(user.id, { avatar_url: url });
            if (!updateError) {
                await fetchProfile();
                addToast("Avatar updated successfully", "success");
            }
        } else if (error) {
            addToast(error, "error");
        }
        setUpdating(false);
    };

    if (loading) {
        return (
            <div className="flex-1 flex items-center justify-center bg-black">
                <Loader2 className="w-10 h-10 text-primary animate-spin" />
            </div>
        );
    }

    return (
        <div className="flex-1 flex flex-col bg-black h-full overflow-hidden">
            <div className="flex-1 overflow-y-auto custom-scrollbar">
                {/* Header Section */}
                <div className="pt-12 pb-8 flex flex-col items-center">
                    <div className="relative">
                        <div className="w-[140px] h-[140px] rounded-full p-1 bg-gradient-to-br from-primary to-secondary">
                            <Avatar src={profile?.avatar_url} fallback={profile?.username?.[0] || "?"} className="w-full h-full rounded-full border-4 border-black border-none" />
                        </div>
                        <button
                            onClick={() => fileInputRef.current?.click()}
                            className="absolute bottom-1 right-1 w-8 h-8 bg-primary rounded-full flex items-center justify-center border-2 border-black text-black hover:scale-110 transition-transform"
                        >
                            <Camera className="w-4 h-4" />
                        </button>
                        <input
                            ref={fileInputRef}
                            type="file"
                            className="hidden"
                            accept="image/*"
                            onChange={handleAvatarUpload}
                        />
                    </div>

                    <div className="mt-8 text-center">
                        <h1 className="text-3xl font-black tracking-tighter flex items-center justify-center gap-2">
                            <span className="text-primary uppercase">{profile?.full_name?.split(' ')[0]}</span>
                            <span className="text-secondary uppercase">{profile?.full_name?.split(' ').slice(1).join(' ')}</span>
                        </h1>
                        <p className="text-white/40 text-sm mt-1 lowercase font-medium">{user?.email}</p>
                    </div>
                </div>

                {/* Content Section */}
                <div className="px-6 space-y-8 pb-32">
                    {/* Personal Info */}
                    <div className="space-y-4">
                        <div className="flex items-center justify-between px-2">
                            <div className="flex items-center gap-3">
                                <h2 className="text-[14px] font-black text-white/50 uppercase tracking-[0.3em]">
                                    <span className="text-primary">PERSONAL</span> <span className="text-secondary">INFO</span>
                                </h2>
                                <img
                                    src="https://hebbkx1anhila5yf.public.blob.vercel-storage.com/image-7u7lTfP9RXZeL7eXf0U0Wf0zQv1R2b.png"
                                    alt=""
                                    className="w-8 h-8 -mt-1 opacity-50 contrast-125 brightness-150 grayscale invert"
                                />
                            </div>
                            {!isEditing && (
                                <button
                                    onClick={() => setIsEditing(true)}
                                    className="px-4 py-1.5 bg-primary/10 text-primary text-[11px] font-black rounded-full hover:bg-primary/20 transition-all uppercase tracking-widest"
                                >
                                    Edit
                                </button>
                            )}
                        </div>

                        <div className="bg-[#0A0A0A] border border-white/5 rounded-[32px] overflow-hidden">
                            {isEditing ? (
                                <div className="p-6 space-y-6">
                                    <div className="space-y-4">
                                        <div className="space-y-1.5">
                                            <label className="text-[10px] font-black text-white/20 uppercase tracking-widest ml-4">Full Name</label>
                                            <input
                                                value={formData.full_name}
                                                onChange={(e) => setFormData({ ...formData, full_name: e.target.value })}
                                                placeholder="Full Name"
                                                className="w-full bg-white/5 border border-white/5 rounded-2xl p-4 text-white font-bold outline-none focus:border-primary/50"
                                            />
                                        </div>
                                        <div className="space-y-1.5">
                                            <label className="text-[10px] font-black text-white/20 uppercase tracking-widest ml-4">Username</label>
                                            <input
                                                value={formData.username}
                                                onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                                                placeholder="Username"
                                                className="w-full bg-white/5 border border-white/5 rounded-2xl p-4 text-white font-bold outline-none focus:border-primary/50"
                                            />
                                        </div>
                                        <div className="grid grid-cols-2 gap-4">
                                            <div className="space-y-1.5">
                                                <label className="text-[10px] font-black text-white/20 uppercase tracking-widest ml-4">Age</label>
                                                <input
                                                    type="number"
                                                    value={formData.age || ''}
                                                    onChange={(e) => setFormData({ ...formData, age: parseInt(e.target.value) })}
                                                    placeholder="Age"
                                                    className="w-full bg-white/5 border border-white/5 rounded-2xl p-4 text-white font-bold outline-none focus:border-primary/50"
                                                />
                                            </div>
                                            <div className="space-y-1.5">
                                                <label className="text-[10px] font-black text-white/20 uppercase tracking-widest ml-4">Phone</label>
                                                <input
                                                    value={formData.phone || ''}
                                                    onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                                                    placeholder="Phone"
                                                    className="w-full bg-white/5 border border-white/5 rounded-2xl p-4 text-white font-bold outline-none focus:border-primary/50"
                                                />
                                            </div>
                                        </div>
                                        <div className="space-y-1.5">
                                            <label className="text-[10px] font-black text-white/20 uppercase tracking-widest ml-4">Bio</label>
                                            <textarea
                                                value={formData.bio || ''}
                                                onChange={(e) => setFormData({ ...formData, bio: e.target.value })}
                                                placeholder="Bio"
                                                className="w-full bg-white/5 border border-white/5 rounded-2xl p-4 text-white font-bold outline-none focus:border-primary/50 resize-none h-24"
                                            />
                                        </div>
                                        <div className="space-y-1.5">
                                            <label className="text-[10px] font-black text-white/20 uppercase tracking-widest ml-4">Gender</label>
                                            <div className="grid grid-cols-3 gap-3">
                                                {['Male', 'Female', 'Other'].map((g) => (
                                                    <button
                                                        key={g}
                                                        onClick={() => setFormData({ ...formData, gender: g })}
                                                        className={cn(
                                                            "py-3 rounded-2xl font-bold transition-all border",
                                                            formData.gender === g
                                                                ? "bg-primary text-black border-primary shadow-lg shadow-primary/20"
                                                                : "bg-white/5 text-white/40 border-transparent hover:border-white/10"
                                                        )}
                                                    >
                                                        {g}
                                                    </button>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="flex gap-4 pt-4">
                                        <button
                                            onClick={() => setIsEditing(false)}
                                            className="flex-1 py-4 text-white/40 font-black uppercase tracking-widest text-[12px]"
                                        >
                                            Cancel
                                        </button>
                                        <button
                                            onClick={handleUpdate}
                                            disabled={updating}
                                            className="flex-3 py-4 bg-primary text-black font-black uppercase tracking-widest text-[12px] rounded-2xl shadow-xl shadow-primary/20 flex items-center justify-center gap-2"
                                        >
                                            {updating ? <Loader2 className="w-4 h-4 animate-spin" /> : "Save Changes"}
                                        </button>
                                    </div>
                                </div>
                            ) : (
                                <div className="p-4 space-y-4">
                                    <InfoItem icon={User} label="Full Name" value={profile?.full_name} />
                                    <InfoItem icon={AtSign} label="Username" value={profile?.username} />
                                    <InfoItem icon={Calendar} label="Age" value={profile?.age?.toString()} />
                                    <InfoItem icon={Phone} label="Phone" value={profile?.phone} />
                                    <InfoItem icon={Info} label="Gender" value={profile?.gender} />
                                    <InfoItem icon={Info} label="Bio" value={profile?.bio} />
                                </div>
                            )}
                        </div>
                    </div>

                    {/* Settings */}
                    <div className="space-y-4">
                        <h2 className="text-[14px] font-black text-primary uppercase tracking-[0.3em] px-2">Settings</h2>
                        <div className="bg-[#0A0A0A] border border-white/5 rounded-[32px] p-4 space-y-2">
                            <SettingItem
                                icon={Bell}
                                label="Notifications"
                                action={
                                    <div className="w-12 h-6 bg-primary rounded-full relative flex items-center px-1">
                                        <div className="w-4 h-4 bg-black rounded-full ml-auto" />
                                    </div>
                                }
                            />
                            <SettingItem
                                icon={Languages}
                                label="Language"
                                value="English"
                                showArrow
                            />
                            <SettingItem
                                icon={Ban}
                                label="Blocked Users"
                                onClick={() => setShowBlocked(true)}
                                showArrow
                            />
                        </div>
                    </div>

                    {/* Logout */}
                    <button
                        onClick={logout}
                        className="w-full py-5 bg-[#1A1A1A] text-secondary font-black uppercase tracking-[0.3em] rounded-[24px] border border-white/5 flex items-center justify-center gap-3 hover:bg-red-500/5 hover:border-red-500/20 transition-all group"
                    >
                        <LogOut className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
                        Logout
                    </button>

                    <p className="text-center text-[10px] font-black text-white/20 uppercase tracking-widest pb-12">
                        GOSSIP v1.2.0 â€¢ English
                    </p>
                </div>
            </div>

            {/* Blocked Users Modal */}
            {showBlocked && (
                <div className="fixed inset-0 z-[100] flex items-end justify-center bg-black/40 backdrop-blur-sm">
                    <div className="w-full max-w-2xl bg-[#0A0A0A] border-t border-x border-white/10 rounded-t-[32px] shadow-2xl overflow-hidden animate-in slide-in-from-bottom duration-300 max-h-[80vh] flex flex-col">
                        <div className="p-6 flex items-center justify-between border-b border-white/5">
                            <h2 className="text-xl font-bold tracking-tighter">
                                <span className="text-primary font-black uppercase">Blocked</span>{" "}
                                <span className="text-secondary font-black uppercase">Users</span>
                            </h2>
                            <button
                                onClick={() => setShowBlocked(false)}
                                className="p-1 px-[3px] border-2 border-primary rounded-md text-primary hover:bg-primary/10 transition-colors"
                            >
                                <X className="w-4 h-4 text-primary" strokeWidth={4} />
                            </button>
                        </div>
                        <div className="p-12 flex flex-col items-center justify-center text-center space-y-6">
                            <div className="w-20 h-20 bg-white/5 rounded-3xl flex items-center justify-center">
                                <Ban className="w-10 h-10 text-white/20" />
                            </div>
                            <div className="space-y-2">
                                <p className="text-white/40 font-bold">No one is blocked.</p>
                            </div>
                            <button
                                onClick={() => setShowBlocked(false)}
                                className="text-secondary font-black uppercase tracking-widest text-[13px] mt-8"
                            >
                                Close
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

function InfoItem({ icon: Icon, label, value }: { icon: any, label: string, value?: string }) {
    return (
        <div className="flex items-center gap-4 p-4 rounded-2xl hover:bg-white/[0.02] transition-colors">
            <div className="w-10 h-10 rounded-xl bg-white/5 flex items-center justify-center text-primary ring-1 ring-white/5">
                <Icon className="w-5 h-5" />
            </div>
            <div className="flex-1">
                <p className="text-[10px] font-black text-white/20 uppercase tracking-widest">{label}</p>
                <p className="text-[15px] font-bold text-white uppercase tracking-tight mt-0.5">{value || "Not Set"}</p>
            </div>
        </div>
    );
}

function SettingItem({ icon: Icon, label, value, action, onClick, showArrow }: { icon: any, label: string, value?: string, action?: React.ReactNode, onClick?: () => void, showArrow?: boolean }) {
    return (
        <div
            onClick={onClick}
            className={cn(
                "flex items-center gap-4 p-4 rounded-2xl transition-colors",
                onClick ? "hover:bg-white/[0.02] cursor-pointer" : ""
            )}
        >
            <div className="w-10 h-10 rounded-xl bg-white/5 flex items-center justify-center text-white/40 ring-1 ring-white/5">
                <Icon className="w-5 h-5" />
            </div>
            <div className="flex-1">
                <p className="text-[15px] font-bold text-white tracking-tight">{label}</p>
            </div>
            {value && <p className="text-[13px] font-bold text-secondary mr-2">{value}</p>}
            {action}
            {showArrow && <ChevronRight className="w-4 h-4 text-white/20" />}
        </div>
    );
}
